//
// Option search
// Styles for option search in dropdown mode and responsive adjustments
// - margin and background for dropdown mode
// - border color for dropdown mode and md breakpoint and up
// - margin and background for md breakpoint and up
//

.option-search-wrapper {
  background-color: var(--bs-secondary-bg-subtle);
  // border-bottom: 1px solid var(--bs-secondary);
  // margin-bottom: -8px;
  position: relative;
  // top: -1px;
  @media (min-width: 768px) {
    // md breakpoint and up
    background-color: var(--bs-body-bg);
    margin-bottom: 0px;
    border-bottom: 1px solid var(--bs-secondary-border-subtle);
  }
  input[type="search"]::-webkit-search-decoration,
  input[type="search"]::-webkit-search-cancel-button,
  input[type="search"]::-webkit-search-results-button,
  input[type="search"]::-webkit-search-results-decoration {
    display: none;
  }
}
